* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}
html {
  overflow-x: hidden;
}
body {
  font-family: "Roboto Condensed", sans-serif;
  overflow-x: hidden;
  background-color: #000000;
}
a {
  text-decoration: none;
}
.links {
  width: 100%;
  position: absolute;
  background-color: aqua;
  text-align: center;
  font-size: 2rem;
}
