.musicplayer {
  height: 120vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url(./headerCover.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  margin-bottom: 5%;
}
.musicplayer::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 120vh;
  /* top: 0; */
  left: 0;
  background-color: rgba(0, 0, 0, 0.472);
}
.homebtn {
  position: absolute;
  color: #e1dedb;
  border-radius: 10px;
  font-size: 1.8rem;
  text-align: center;
  background-color: black;
  top: 2%;
  left: 80%;
  padding: 2px 3px;
  transition: all 0.5s;
}
.homebtn:hover {
  color: black;
  background-color: #e1dedbb0;
}
.musicplayerAPP {
  background-color: rgba(240, 248, 255, 0.507);
  width: 80%;
  min-height: 130%;
  position: absolute;
  top: 10%;
  left: 10%;
  display: flex;
  padding: 2%;
  background-size: cover;
  border-radius: 5%;
  background-size: cover;
  box-shadow: inset 0 0 0 2000px rgba(0, 0, 0, 0.555);
}
.musicplayerAPP img {
  width: 25%;
  height: 25%;
  border-radius: 3%;
}
.info {
  color: white;
  margin-top: 5%;
  margin-left: 3%;
}
.musicplayerAPP h2 {
  font-size: 3rem;
  margin-bottom: 5%;
}
.musicplayerAPP p {
  font-size: 1.5rem;
}
.playlist {
  width: 400px;
  border-radius: 3%;
  position: absolute;
  left: 40%;
  top: 30%;
  width: 50%; /*to redo*/
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.playlist p {
  background-color: rgba(255, 255, 255, 0.746);
  padding: 5px;
  padding-left: 10px;
  border-bottom: 1px solid black;
  transition: all 0.3s;
  font-size: 1.3rem;
}
.playlist i {
  font-size: 0.8rem;
  transform: translateY(-20%);
  transition: all 0.3s;
}
.playlist p:hover {
  transform: translateX(10px);
  background-color: white;
  font-size: 1.5rem;
}
.playlist p:hover i {
  font-size: 1rem;
}
.theAlbmus {
  padding: 0 10%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin-bottom: 3%;
}
.albitem {
  width: 20%;
  position: relative;
  transition: all 0.3s;
  margin-bottom: 2%;
}
.albitem:hover {
  width: 21%;
  margin-bottom: 1%;
}
.albitem::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.376);
  width: 100%;
  height: 100%;
  transition: all 0.3s;
}
.albitem:hover::before {
  background-color: rgba(0, 0, 0, 0);
}
.albitem img {
  border-radius: 5%;
  width: 100%;
}

@media only screen and (max-width: 800px) {
  .musicplayer {
    height: 90vh;
  }
  .playlist {
    width: 80%;
    left: 10%;
  }
  .info {
    margin-top: 2%;
    margin-left: 3%;
  }
  .musicplayerAPP h2 {
    font-size: 2rem;
    color: white;
  }
  .musicplayerAPP p {
    font-size: 1rem;
  }
  .homebtn {
    left: 50%;
    transform: translateX(-50%);
  }
  .musicplayerAPP {
    justify-content: center;
    top: 10%;
    min-height: 90%;
  }
}
